* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}


body {
    margin-top: 100px;

    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    font-weight: 500;
    font-style: normal;
    line-height: 24px;
    color: #000000;

    & button {
        cursor: pointer
    }
    .initialized {
        margin: 20% 50%;
    }
    .backdrop {
    color: #fff;
    z-index: 10;
}

}

